import React, {Component} from 'react';
import { PostWrapper, EventPost } from '../../Components';
import { ChainService } from "../../Services";
import Config from "../../Config";

class PostContainer extends Component {
  constructor(props) {
    super();
    this.state = {
      fetching: false, 
      info: null
    };

    this.chain = new ChainService(Config.network);
  }

  fetchItemEventInfo = async () => {
    this.setState({
        fetching: true
    });

    const res = await this.chain.readTable("eosknightsio", "itemevt");
    let info = null;
    if (res.status == 200) {
      info = res.data.rows[0];
    }

    console.log(info);

    this.setState({
      info: info,
      fetching: false
    });    
  }

  componentDidMount() {
    this.fetchItemEventInfo();
  }

  render() {
    const {fetching, info} = this.state;

    return (
      <PostWrapper>
        <EventPost
            title="from test"
            body={info.duration}
        />
      </PostWrapper>
    );
  }
}

export default PostContainer;
